.flexslider {
  border: none;
  padding: 0rem 15rem;
  background-color: #fcfff7;
}
.flexslider .flex-viewport .slides {
  min-height: 512px;
  max-height: 100vh;
  width: 100%;
  display: block;
  list-style: none;
}
.flexslider .flex-viewport .slides li {
  width: initial !important;
  max-width: 33.3333333333vw;
}

.conference-slider {
  padding: 40px 20px;
}
.conference-slider header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.conference-slider header h2 {
  font-size: 3rem;
  margin-bottom: 10px;
}
.conference-slider header p {
  font-size: 2rem;
  color: grey;
  margin-bottom: 50px;
  margin-top: 10px;
}

.quicktabs-wrapper .quicktabs-main .quicktabs-tabpage {
  border: none;
  padding: 2.5rem 1.5rem;
}
.quicktabs-wrapper .quicktabs-tabs .slider-tabs {
  border-bottom: 2px solid #1a8fe3;
}
.quicktabs-wrapper .quicktabs-tabs .slider-tabs a {
  background: none;
  border: none;
  padding: 10px;
  font-weight: 700;
  font-size: 1.5em;
  color: #1a8fe3;
}
.quicktabs-wrapper .quicktabs-tabs .active a {
  background: none;
  border: none;
  padding: 10px;
  font-weight: 700;
  font-size: 2em;
  color: black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}
.quicktabs-wrapper .quicktabs-tabs .active a::after {
  content: "";
  border-left: 2px solid #1a8fe3;
  border-bottom: 2px solid #1a8fe3;
  width: 15px;
  height: 15px;
  transform: rotate(-45deg);
  position: absolute;
  top: 90%;
}
.quicktabs-wrapper .quicktabs-tabs .active a::before {
  content: "";
  border-bottom: 2px solid #fcfff7;
  width: 21px;
  height: 15px;
  position: absolute;
  bottom: -2px;
}